import './App.css';
import Todo from './Todo';
import { useState ,useEffect} from 'react';
import {Container,List,Paper,Grid,Button,AppBar,Toolbar,Typography} from "@mui/material";
import AddTodo from './AddTodo';
import {call,siginout} from "./service/ApiService";

function App() {
  const[items,setItems] = useState([]);
  const[loading, setLoading] = useState(true);


  useEffect(() => {
    call("/todo","GET",null)
    .then((response) => setItems(response.data));
    setLoading(false);
  },[]);

  
  
  const addItem = (item) => {
   // item.id = "ID-" + items.length; // key  id
   // item.done = false; // done 
    //   setItems      .
   // setItems([...items, item]); 
   // console.log("items : ", items);
    call("/todo","POST",item)
    .then((response) => setItems(response.data));
  };

  const deleteItem = (item) => {
    //  .
    //const newItems = items.filter(e => e.id !== item.id);
    //       . 
    //setItems([...newItems]);
    call("/todo","DELETE",item)
    .then((response) => setItems(response.data));
  }

  const editItem =(item)=>{
    //setItems([...items]);
    call("/todo","PUT",item)
    .then((response) => setItems(response.data));
  };

//let todoItems = items.length > 0 && items.map((item)=><Todo item={item} key={item.id}/>);
let todoItems = items.length > 0 && (
  <Paper style={{ margin: 16 }}>
    <List>
      {items.map((item) => (
        <Todo item={item} key={item.id} editItem={editItem} deleteItem={deleteItem} />
      ))}
    </List>
  </Paper>
);

// navigationbar 
let navigationBar = (
  <AppBar position='static'>
    <Toolbar>
      <Grid justifyContent="space-between" container>
        <Grid item>
          <Typography variant='h6'> </Typography>
        </Grid>
        <Grid item>
          <Button color="inherit" raised onClick={siginout}>
            
          </Button>
        </Grid>
      </Grid>
    </Toolbar>
  </AppBar>
);

/*      */
let todoListPage = (
  <div>
    {navigationBar} {/*   */}
    <Container maxWidth="md">
      <AddTodo addItem={addItem}/>
      <div className='TodoList'>{todoItems}</div>
    </Container>
  </div>
);
  /*      */
  let loadingPage=<h1>...</h1>;
  let content = loadingPage;

  if(!loading){
    /*  todoListPage */
    content = todoListPage;
  }

  /*  content  */
  return <div className='App'>{content}</div>

}

export default App;
