import React from "react";
import { Container, Grid, Typography, TextField, Button } from "@mui/material";
import { Link } from "react-router-dom";
import { signin } from "./service/ApiService";

function Login(){
    const handleSubmit = (event) =>{
        event.preventDefault();
        const data = new FormData(event.target);
        const username = data.get("username");
        const password = data.get("password");
        // ApiSerivce signin   .
        signin({username: username,password: password});
    };

    return (
        <Container component="main" maxWidth="xs" style={{ marginTop: "8%"}}>
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Typography component="h1" variant="h5">
                        
                    </Typography>
                </Grid>
            </Grid>
            <form noValidate onSubmit={handleSubmit}>
                {" "}
                {/* submit   handleSubmit . */}
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <TextField variant="outlined" required fullWidth id="username" label="" name="username" autoComplete="username"/>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField variant="outlined" required fullWidth name="password" label="" type="password" id="password" autoComplete="current-password"/>
                    </Grid>
                    <Grid item xs={12}>
                        <Button type="submit" fullWidth variant="contained" color="primary"></Button>
                    </Grid>
                    <Grid item>
                        <Link to="/signup" variant="body2">
                             ?   
                        </Link>
                    </Grid>
                </Grid>
            </form>
        </Container>
    );
};

export default Login;
